export default {
    namespaced: true,
    state: {
        lines: []
    },
    getters: {
        itemCount: state => state.lines.reduce((total, line) =>
            total + line.quantity, 0),
        totalPrice: state => state.lines.reduce((total, line) =>
            total + (line.quantity * line.product.price), 0),
    },
    mutations: {
        addProduct(state, product) {
            let line = state.lines.find(line => line.product.id == product.id); if (line != null) {
                line.quantity++;
            } else {
                state.lines.push({ product: product, quantity: 1 });
            }
        },
        changeQuantity(state, update) {
            update.line.quantity = update.quantity;
        },
        removeProduct(state, lineToRemove) {
            let index = state.lines.findIndex(line => line == lineToRemove); if (index > -1) {
                state.lines.splice(index, 1);
            }
        },
        setCartData(state, data) {
            state.lines = data;
        }
    },
    // Actions are not allowed to modify state data directly in the data store, 
    // so I have also added a mutation that sets the lines property.
    //The most important addition in Listing 6 - 11 is the initializeCart action, 
    //which is responsible
    // for dealing with the cart when the application starts.
    
    actions: {
        loadCartData(context) {
            let data = localStorage.getItem("cart");
            if (data != null) {
                context.commit("setCartData", JSON.parse(data));
            }
        },
        storeCartData(context) {
            localStorage.setItem("cart", JSON.stringify(context.state.lines));
        },
        clearCartData(context) {
            context.commit("setCartData", []);
        },
        initializeCart(context, store) {
            context.dispatch("loadCartData");
            //When the action is invoked, the first statement calls the dispatch method, which is how actions are programmatically invoked.To observe the data store
            // for changes to the lines state property, I used the watch method, 
            //like this:
            store.watch(state => state.cart.lines,
                () => context.dispatch("storeCartData"), {deep: true});
        }
    }
}