<template>
    <div>
        <div class="container-fluid">
            <div class="row">
                <div class="col bg-dark text-white">
                    <a class="navbar-brand">SPORTS STORE</a> 
                </div>
            </div>
        </div>
        <div class="m-2">
            <div class="form-group m-2">
                <label>Name</label>
                
                <!-- <input v-model="$v.name.$model" class="form-control "/>
                <validation-error v-bind:validation="$v.name" /> -->
                <input v-model="$v.order.name.$model" class="form-control "/>
                <validation-error v-bind:validation="$v.order.name" />
            </div>
        </div>
        <!--  -->
        <div class="m-2">
            <div class="form-group m-2">
                <label>Email</label>
                <input v-model="$v.order.email.$model" class="form-control "/> <validation-error v-bind:validation="$v.order.email" />
                </div>
            </div>
            <div class="m-2">
                <div class="form-group m-2">
                <label>Address</label>
                <input v-model="$v.order.address.$model" class="form-control "/> <validation-error v-bind:validation="$v.order.address" />
            </div>
        </div>
        <div class="m-2">
            <div class="form-group m-2">
                <label>City</label>
                <input v-model="$v.order.city.$model" class="form-control "/> <validation-error v-bind:validation="$v.order.city" />
            </div>
        </div>
        <div class="m-2">
            <div class="form-group m-2">
                <label>Zip</label>
                <input v-model="$v.order.zip.$model" class="form-control "/> <validation-error v-bind:validation="$v.order.zip" />
            </div>
        </div>
<!--  -->
        <div class="text-center">
            <router-link to="/cart" class="btn btn-secondary m-1"> Back
            </router-link>
            <button class="btn btn-primary m-1" v-on:click="submitOrder">
                Place Order
            </button>
        </div>
    </div>
</template>
<script>
import { required, email } from "vuelidate/lib/validators"; 
import ValidationError from "./ValidationError";

import { mapActions } from "vuex";

export default {
    components: { ValidationError },

    // this component has its own local data, 
    // which is not shared with any other component. 
    // This is defined using the data property 
    // in the script element and must be expressed in unusual way.
    
    // This fragment of code defines a single local data property called name, 
    // whose initial value is null. You will become used to this expression 
    // as you become experienced 
    // in Vue.js development and it soon becomes second nature.
    data: function() {
        return   {   
            order: { 
                name: null,
                email: null,
                address: null,
                city: null,
                zip: null
            }
        }
    },
    validations: {
        order: {
            name: { required },
            email: { required, email },
            address: { required },
            city: { required },
            zip: { required }
        }
    },
    methods: {
        ...mapActions({
            "storeOrder": "storeOrder", 
            "clearCart": "cart/clearCartData"
        }),
        async submitOrder() {
            // The $v object defines a $touch method, which marks all 
            // of the elements as dirty, as though the user had edited them. 
            // This is a useful feature to trigger validation as 
            // the result of a user action, while not usually displaying 
            // validation messages 
            // until the user interacts with an input element
            this.$v.$touch();


            if (!this.$v.$invalid) {
                let order = await this.storeOrder(this.order); 
                this.clearCart(); 
                this.$router.push(`/thanks/${order}`);
            }
        } 
    }
}
</script>