import Vue from "vue";
import VueRouter from "vue-router";
import Store from "../components/Store";
import ShoppingCart from "../components/ShoppingCart";
import Checkout from "../components/Checkout";
import OrderThanks from "../components/OrderThanks";
import Authentication from "../components/admin/Authentication";
import Admin from "../components/admin/Admin";

import ProductAdmin from "../components/admin/ProductAdmin";
import OrderAdmin from "../components/admin/OrderAdmin";
import ProductEditor from "../components/admin/ProductEditor";

// The
// import statement provides me with access to the data store, through which I can read the value of the state property that the route guard needs 
// to determine whether the user has been authenticated.
import dataStore from "../store"; //to check if authenticated.

Vue.use(VueRouter);

export default new VueRouter({
    mode: "history",
    routes: [{
            path: "/",
            component: Store
        },
        {
            path: "/cart",
            component: ShoppingCart
        },
        { 
            path: "/checkout", component: Checkout 
        },
        { 
            path: "/thanks/:id", component: OrderThanks 
        },
        {
            path: "/login",
            component: Authentication
        }, {
            path: "/admin",
            component: Admin,
            // a guard that is specific to the route
            // for the / admin URL and that will prevent navigation
            // if the user hasn’ t been authenticated
            beforeEnter(to, from, next) {
                if (dataStore.state.auth.authenticated) {
                    next();
                } else {
                    next("/login");
                }
            },
            // use the child route feature, which allows nested router - view elements
            children: [{
                    path: "products/:op(create|edit)/:id(\\d+)?",
                    component: ProductEditor
                }, {
                    path: "products",
                    component: ProductAdmin
                }, {
                    path: "orders",
                    component: OrderAdmin
                },
                {
                    path: "",
                    redirect: "/admin/products"
                }
            ]
        },
        {
            path: "*",
            redirect: "/"
        }
    ]
})